body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-color: black
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
      
  }
  
.board-row{
           width:60%;
           margin-left:20%;
           display:flex;
           justify-content: center;
}
  .status {
    margin-bottom: 10px;
    text-align:center;
    font-size:40px;
    font-family: 'Noto Sans JP', sans-serif;
    color:white
      
    
  }
  

.game-board{
            width:100%;
            
}
  .square {
    background: #fff;
    border: 3px solid #999;
    font-size: 50px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    line-height: 34px;
    height: 170px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 30%;
    font-family: 'Rowdies', cursive;
    background-image:linear-gradient(to bottom right, white, #ccc);
    
    
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  
